.App {
  text-align: center;
  display: column;
  height: 100%;
  width: 100%;
}

.header {
  display: flex;
  justify-content: space-around;
  height: 150px;
  width:100%;
}

.content {
  display: flex;
  width: 100%;
  height: 100%;
}

.sidebar-container {
  width: 200px;
  height:500px;
}

.routes {
  display:flex;
  flex-direction: column;
  width: 80%;
  height: 500px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
